.Admin_login {
    width: 100%;
    min-height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Admin_login .Admin_login-content {
    width: 25%;
    height: min-content;
    text-align: center;
    font-family: Poppins;
}

.Admin_login .Admin_login-content i {
    font-size: 100px;
    border: 1px solid #000;
    border-radius: 50%;
    padding: 10px;
    margin-bottom: 30px;
}

.Admin_login .Admin_login-content .form-control {
    border: 0;
    border-bottom: 1px solid lightgray;
    border-radius: 0;
    margin-bottom: 50px;
}

.Admin_login .Admin_login-content .btn {
    background: #57b846;
    color: #fff;
    display: block;
    width: 100%;
    border-radius: 30px;
    box-shadow: 0 10px 30px 0 rgb(87 184 70 / 50%);
    padding: 10px;
    transition: .5s;
}

.Admin_login .Admin_login-content .btn:hover {
    background-color: #000;
    box-shadow: 0 10px 30px 0 rgb(51 51 51 / 50%);
}